.nav{
    background-color:#87e0ec;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.menu-bars {
    margin-left: 2rem;
    font-size:2rem;
    background: none;
}
.nav-name{
    text-align: center;
    margin-left: 3%;
    margin-top: 1.5%;
    font-size: 2vw;
    color: #fff;
}

.nav-menu{
    background-color: #87e0ec;
    width:250px;
    height: 100vh;
    display: flex;
    justify-content:center;
    position: fixed;
    top:0;
    left: -100%;
    transition: 850ms;
}
.nav-menu.active{
    left: 0;
    transition: 350ms;
}

.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 9px 0px 9px 14px;
    list-style: none;
    height: 60px;

}
.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}
.nav-text a:hover{
    background-color: #1a83ff;
}
.nav-menu-items {
    width: 100%;
}
.navbar-toggle {
    background-color: #87e0ec;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}
span{
    margin-left: 16px;
}